// storage.js
const PREFIX = 'acula';

 function getPrompts() {
    return JSON.parse(localStorage.getItem(`${PREFIX}prompts`) || '[]');
}

 function savePrompts(prompts) {
    localStorage.setItem(`${PREFIX}prompts`, JSON.stringify(prompts));
}

 function getLastSelectedPromptIndex() {
    return parseInt(localStorage.getItem('lastSelectedPromptIndex') || '-1', 10);
}

 function saveLastSelectedPromptIndex(index) {
    localStorage.setItem('lastSelectedPromptIndex', index.toString());
}

//    
 function getLastSelectedMultipromptIndices() {
    const indices = localStorage.getItem('lastSelectedMultipromptIndices');
    return indices ? JSON.parse(indices) : [];
}

 function saveLastSelectedMultipromptIndices(indices) {
    localStorage.setItem('lastSelectedMultipromptIndices', JSON.stringify(indices));
}

// wordInfo.js

 async function getWordsInfo(context, selection) {
    const ranges = selection.getTextRanges([" "], false);
    ranges.load("items");
    await context.sync();

    let wordsInfo = [];

    console.log(ranges)

    for (let range of ranges.items) {
        range.load("text, hyperlink");
        range.font.load("bold, italic, underline, color, highlightColor, size, name");
        const footnotes = range.footnotes;
        footnotes.load("items");

        await context.sync();

        if (range.text.trim()) {
            let footnoteText = "";
            if (footnotes.items.length > 0) {
                const footnote = footnotes.items[0];
                footnote.body.load("text");
                await context.sync();
                footnoteText = footnote.body.text;
            }

            wordsInfo.push({
                text: range.text.trim(),
                bold: range.font.bold,
                italic: range.font.italic,
                underline: range.font.underline !== "None",
                color: range.font.color || null,
                highlightColor: range.font.highlightColor || null,
                size: range.font.size || null,
                fontName: range.font.name || null,
                footnoteText: footnoteText || null,
                hyperlink: range.hyperlink ? range.hyperlink : null
            });
        }
    }

    return wordsInfo;
}

 async function applyFormattedText(context, selection, newText, formattingDifferences) {
    selection.clear();
    await context.sync();

    //      
    const defaultFormatting = calculateDefaultFormattingFromDifferences(formattingDifferences);

    selection.font.bold = defaultFormatting.bold;
    selection.font.italic = defaultFormatting.italic;
    selection.font.underline = defaultFormatting.underline ? 'Single' : 'None';
    selection.font.color = defaultFormatting.color || 'black';
    selection.font.highlightColor = defaultFormatting.highlightColor || null;
    selection.font.size = defaultFormatting.size || 11;
    selection.font.name = defaultFormatting.fontName || 'Calibri';
    await context.sync();

    const words = newText.split(/\s+/);

    // ,     
    let formattingMap = new Map();
    if (Array.isArray(formattingDifferences) && formattingDifferences.length > 0) {
        //       
        formattingDifferences.forEach((item, index) => {
            const key = `${item.text}-${index}`;
            formattingMap.set(key, item);
        });
    } else {
        //     ,   ()
        console.warn('   .    .');
    }

    let wordIndex = 0;

    for (let i = 0; i < words.length; i++) {
        let word = words[i];

        //         
        let range = selection.insertText(word, Word.InsertLocation.end);

        //     range
        // ...

        // ,       
        const key = `${word}-${wordIndex}`;
        const wordInfo = formattingMap.get(key);

        if (wordInfo) {
            //    
            for (let prop in wordInfo) {
                if (prop !== 'text') {
                    if (prop === 'bold') range.font.bold = wordInfo[prop];
                    if (prop === 'italic') range.font.italic = wordInfo[prop];
                    if (prop === 'underline') range.font.underline = wordInfo[prop] ? 'Single' : 'None';
                    if (prop === 'color') range.font.color = wordInfo[prop];
                    if (prop === 'highlightColor') range.font.highlightColor = wordInfo[prop];
                    if (prop === 'size') range.font.size = wordInfo[prop];
                    if (prop === 'fontName') range.font.name = wordInfo[prop];
                    if (prop === 'hyperlink') range.hyperlink = wordInfo[prop];
                    if (prop === 'footnoteText' && wordInfo[prop]) {
                        let newFootnote = range.insertFootnote();
                        newFootnote.body.insertText(wordInfo[prop], Word.InsertLocation.start);
                    }
                }
            }
            wordIndex++;
        }


        //     ,  
        if (i < words.length - 1) {
            selection.insertText(" ", Word.InsertLocation.end);
        }

        await context.sync();
    }
}


//      
function createFormattingMap(formattingDifferences) {
    const map = {};
    formattingDifferences.forEach((item, index) => {
        const key = `${item.text}-${index}`; //      
        map[key] = item;
    });
    return map;
}

//        
function calculateDefaultFormattingFromDifferences(formattingDifferences) {
    //       
    //  ,     
    return {
        bold: false,
        italic: false,
        underline: false,
        color: 'black',
        highlightColor: null,
        size: 11,
        fontName: 'Calibri',
    };
}

 async function showSelectedTextInfo() {
    await Word.run(async (context) => {
        const selection = context.document.getSelection();
        const ranges = selection.getTextRanges([" "], false);
        // console.log(ranges)
        ranges.load("items");

        await context.sync();

        let wordsInfo = [];
        let colorCounts = {};
        let fontCounts = {};
        let sizeCounts = {};

        for (let i = 0; i < ranges.items.length; i++) {
            const range = ranges.items[i];
            range.load("text, hyperlink");
            range.font.load("bold, italic, underline, color, highlightColor, name, size");

            const footnotes = range.footnotes;
            footnotes.load("items");

            await context.sync();

            if (range.text.trim()) {
                const color = range.font.color || "default";
                const font = range.font.name || "default";
                const size = range.font.size || "default";

                colorCounts[color] = (colorCounts[color] || 0) + 1;
                fontCounts[font] = (fontCounts[font] || 0) + 1;
                sizeCounts[size] = (sizeCounts[size] || 0) + 1;

                let footnoteText = "";
                if (footnotes.items.length > 0) {
                    const footnote = footnotes.items[0];
                    footnote.body.load("text");
                    await context.sync();
                    footnoteText = footnote.body.text;
                }

                wordsInfo.push({
                    text: range.text.trim(),
                    bold: range.font.bold,
                    italic: range.font.italic,
                    underline: range.font.underline !== "None",
                    color: color,
                    highlightColor: range.font.highlightColor,
                    hasFootnote: footnotes.items.length > 0,
                    footnoteText: footnoteText,
                    fontName: font,
                    fontSize: size,
                    hyperlink: range.hyperlink ? range.hyperlink.address : null,
                });
            }
        }

        const baseColor = getMostCommonValue(colorCounts);
        const baseFont = getMostCommonValue(fontCounts);
        const baseSize = getMostCommonValue(sizeCounts);

        displayWordsInfo(wordsInfo, baseColor, baseFont, baseSize);
    });
}

function getMostCommonValue(counts) {
    return Object.entries(counts).reduce((a, b) => a[1] >= b[1] ? a : b)[0];
}

 function displayWordsInfo(wordsInfo, baseColor, baseFont, baseSize) {
    const infoDiv = document.getElementById("fuuga_selected-text-info");
    let html = "<h3>  :</h3>";

    wordsInfo.forEach((word, index) => {
        html += `<h4> ${index + 1}: ${word.text}</h4>`;
        html += `<p>
          ${word.bold ? "" : ""} <strong></strong><br>
          ${word.italic ? "" : ""} <em></em><br>
          ${word.underline ? "" : ""} <u></u><br>
          ${word.color !== baseColor && word.color !== "default" ? `  : ${word.color}` : "  "}<br>
          ${word.highlightColor ? ` : ${word.highlightColor}` : "  "}<br>
          ${word.footnoteText ? `  : ${word.footnoteText}` : "  "}<br>
          ${word.fontName !== baseFont && word.fontName !== "default" ? `  : ${word.fontName}` : `  : ${baseFont}`}<br>
          ${word.size != baseSize && word.size !== "default" ? `  : ${word.size}` : `  : ${baseSize}`}<br>
          ${word.hyperlink ? ` : <a href="${word.hyperlink}" target="_blank">${word.hyperlink}</a>` : "  "}
        </p>`;
    });

    infoDiv.innerHTML = html;
}

//       
function getSpecialFormattingWords(formattingInfo, defaultFormatting) {
    return formattingInfo
        .map(item => {
            const differences = { text: item.text };
            let hasDifferences = false;
            for (let prop in defaultFormatting) {
                if (item[prop] !== undefined && item[prop] !== defaultFormatting[prop]) {
                    differences[prop] = item[prop];
                    hasDifferences = true;
                }
            }
            return hasDifferences ? differences : null;
        })
        .filter(item => item !== null);
}


async function getUpdatedFormattingInfo(originalFormattingInfo, originalText, modifiedText, formattingModel, temperature, maxTokens, signal) {
    const apiKey = localStorage.getItem('apiKey');

    //      
    const defaultFormatting = calculateDefaultFormatting(originalFormattingInfo);

    //        
    const specialWords = getSpecialFormattingWords(originalFormattingInfo, defaultFormatting);

    //       
    const systemPrompt = ` - AI    .   -              ,     (,  ).          .`;

    const userPrompt = `
 : "${originalText}"
 : "${modifiedText}"

       :
${JSON.stringify(specialWords, null, 2)}

  (     ):
${JSON.stringify(defaultFormatting, null, 2)}

 :
1.          ,     (, ).
2.          .
3.  -       ,  .

 ****  JSON-,    :
- text: string -       
-  ,    

 :
[
  { "text": "Hi", "italic": true },
  { "text": "Danil", "bold": true },
  { "text": "20", "size": 20 }
]
`;

    const requestBody = {
        model: formattingModel,
        messages: [
            { role: "system", content: systemPrompt },
            { role: "user", content: userPrompt }
        ],
        temperature: temperature,
        max_tokens: maxTokens,
    };

    try {
        const response = await fetch("https://openrouter.ai/api/v1/chat/completions", {
            method: 'POST',
            headers: {
                'Authorization': `Bearer ${apiKey}`,
                'Content-Type': 'application/json',
                'HTTP-Referer': 'https://www.office.com',
                'X-Title': 'Word Text Formatter',
            },
            body: JSON.stringify(requestBody),
            signal: signal,
        });

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        console.log('   ():', data.choices[0].message.content.trim());

        const formattingResponse = data.choices[0].message.content.trim();

        //   
        let updatedFormattingInfo;
        try {
            updatedFormattingInfo = JSON.parse(formattingResponse);
            if (!Array.isArray(updatedFormattingInfo)) {
                throw new Error("Response is not an array");
            }
        } catch (parseError) {
            console.error('    :', parseError);
            throw parseError;
        }

        return updatedFormattingInfo;

    } catch (error) {
        if (error.name === 'AbortError') {
            console.log('  .');
            throw error;
        } else {
            console.error('Error in getUpdatedFormattingInfo:', error);
            throw error;
        }
    }
}


//  

function calculateDefaultFormatting(formattingInfo) {
    const counts = {
        bold: {},
        italic: {},
        underline: {},
        color: {},
        highlightColor: {},
        size: {},
        fontName: {},
    };

    formattingInfo.forEach(item => {
        for (let prop in counts) {
            const value = item[prop] !== undefined ? item[prop] : null;
            counts[prop][value] = (counts[prop][value] || 0) + 1;
        }
    });

    const defaultFormatting = {};
    for (let prop in counts) {
        defaultFormatting[prop] = Object.entries(counts[prop]).reduce((a, b) => a[1] >= b[1] ? a : b)[0];
        //   'true', 'false', 'null'    
        if (defaultFormatting[prop] === 'true') defaultFormatting[prop] = true;
        else if (defaultFormatting[prop] === 'false') defaultFormatting[prop] = false;
        else if (defaultFormatting[prop] === 'null') defaultFormatting[prop] = null;
        else if (prop === 'size' && defaultFormatting[prop] !== null) defaultFormatting[prop] = parseFloat(defaultFormatting[prop]);
    }

    return defaultFormatting;
}


function getFormattingDifferences(originalFormattingInfo, defaultFormatting) {
    return originalFormattingInfo.map(item => {
        const differences = { text: item.text };
        for (let prop in defaultFormatting) {
            if (item[prop] !== undefined && item[prop] !== defaultFormatting[prop]) {
                differences[prop] = item[prop];
            }
        }
        return differences;
    });
}

 { getUpdatedFormattingInfo };
