// ... (Other functions like formatPrompt, updatePromptSelect, etc.)
 function updatePromptSelect() {
  const select = $('#fuuga_prompt-select');
  const modelDiv = document.getElementById('fuuga_selected-model');
  const prompts = getPrompts();
  select.empty();
  select.append(new Option(' ', '', true, true));
  const mainSettings = JSON.parse(localStorage.getItem('mainSettings') || '{}');
  const lastSelectedPromptIndex = mainSettings.lastSelectedPromptIndex || -1;
  prompts.forEach((prompt, index) => {
      const option = new Option(prompt.name, index, false, index == lastSelectedPromptIndex);
      select.append(option);
  });
  select.trigger('change');
  const promptIndex =
      lastSelectedPromptIndex >= 0 && lastSelectedPromptIndex < prompts.length
          ? lastSelectedPromptIndex
          : null;
  lastSelectedPrompt = promptIndex !== null ? prompts[promptIndex] : null;
  modelDiv.textContent = lastSelectedPrompt ? `: ${lastSelectedPrompt.model}` : '';
  document.getElementById('fuuga_process-text').disabled = !lastSelectedPrompt;
  select.on('change', function () {
      const newPromptIndex = $(this).val();
      lastSelectedPrompt = newPromptIndex ? prompts[newPromptIndex] : null;
      const mainSettings = JSON.parse(localStorage.getItem('mainSettings') || '{}');
      mainSettings.lastSelectedPromptIndex = newPromptIndex || -1;
      localStorage.setItem('mainSettings', JSON.stringify(mainSettings));
      modelDiv.textContent = lastSelectedPrompt ? `: ${lastSelectedPrompt.model}` : '';
      document.getElementById('fuuga_process-text').disabled = !lastSelectedPrompt;
      saveHomeSettingsHome();
  });
}

function formatPrompt(prompt) {
  if (!prompt.id) {
      return prompt.text;
  }
  const prompts = getPrompts();
  const promptData = prompts[prompt.id];
  if (!promptData) return prompt.text;
  return $(`<span>${promptData.name} (${promptData.model})</span>`);
}

function formatPromptSelection(prompt) {
  if (!prompt.id) {
      return prompt.text;
  }
  const prompts = getPrompts();
  const promptData = prompts[prompt.id];
  if (!promptData) return prompt.text;
  return promptData.name;
}


