function loadModelsForFormattingSelect() {
  const select = $('#fuuga_formatting-model');
  const models = JSON.parse(localStorage.getItem('models') || '[]');
  select.empty();
  models.forEach((model) => {
      const option = new Option(model.name, model.id, false, false);
      select.append(option);
  });
  select.trigger('change');

  // Set default value
  const defaultModel = models.find((model) => model.id === 'openai/gpt-4') || models[0];
  if (defaultModel) {
      select.val(defaultModel.id).trigger('change');
  }

  // Load saved model
  const mainSettings = JSON.parse(localStorage.getItem('mainSettings') || '{}');
  const savedModel = mainSettings.formattingModel;
  if (savedModel && models.some((model) => model.id === savedModel)) {
      select.val(savedModel).trigger('change');
  }
}

function loadModelsForLoggerSelectHome() {
  const select = $('#fuuga_logger-model');
  const models = JSON.parse(localStorage.getItem('models') || '[]');
  select.empty();
  models.forEach((model) => {
      const option = new Option(model.name, model.id, false, false);
      select.append(option);
  });
  select.trigger('change');

  // Set default value
  const defaultModel = models.find((model) => model.id === 'openai/gpt-4') || models[0];
  if (defaultModel) {
      select.val(defaultModel.id).trigger('change');
  }

  // Load saved model
  const mainSettings = JSON.parse(localStorage.getItem('mainSettings') || '{}');
  const savedModel = mainSettings.loggerModel;
  if (savedModel && models.some((model) => model.id === savedModel)) {
      select.val(savedModel).trigger('change');
  }
}

function checkSelection() {
  Word.run(async (context) => {
      const selection = context.document.getSelection();
      selection.load('text');
      await context.sync();

      const processButton = document.getElementById('fuuga_process-text');
      const processMultipromptButton = document.getElementById('fuuga_process-multiprompt-text');
      const selectedPromptIndices = $('#fuuga_multiprompt-select').val();
      if (selection.text.trim().length > 0 && lastSelectedPrompt) {
          processButton.disabled = false;
      } else {
          processButton.disabled = true;
      }
      if (
          selection.text.trim().length > 0 &&
          selectedPromptIndices &&
          selectedPromptIndices.length > 0
      ) {
          processMultipromptButton.disabled = false;
      } else {
          processMultipromptButton.disabled = true;
      }
  });
}

function updateMultipromptSelect() {
  const select = $('#fuuga_multiprompt-select');
  const prompts = getPrompts();
  select.empty();

  prompts.forEach((prompt, index) => {
      const option = new Option(prompt.name, index, false, false);
      select.append(option);
  });

  // Load saved multiprompt indices
  const mainSettings = JSON.parse(localStorage.getItem('mainSettings') || '{}');
  const lastSelectedMultipromptIndices = mainSettings.lastSelectedMultipromptIndices || [];

  // Set selected options
  select.val(lastSelectedMultipromptIndices).trigger('change');

  // Update selected prompts list based on saved indices
  updateSelectedPromptsList(lastSelectedMultipromptIndices);

  // Ensure event handlers are set
  select.off('change').on('change', function () {
      const selectedPromptIndices = $(this).val();
      updateSelectedPromptsList(selectedPromptIndices);
      saveHomeSettingsHome();
  });
}

function updateSelectedPromptsList(selectedPromptIndices) {
  const prompts = getPrompts();
  const selectedPromptsList = document.getElementById('fuuga_selected-prompts-list');
  selectedPromptsList.innerHTML = '';

  if (!Array.isArray(selectedPromptIndices)) {
      selectedPromptIndices = selectedPromptIndices ? [selectedPromptIndices] : [];
  }

  selectedPromptIndices.forEach((indexStr) => {
      if (indexStr === '') {
          return;
      }

      const index = parseInt(indexStr, 10);

      if (isNaN(index)) {
          return;
      }

      const prompt = prompts[index];
      if (!prompt) {
          return;
      }

      const listItem = document.createElement('li');
      listItem.className = 'list-group-item d-flex justify-content-between align-items-center';
      listItem.dataset.promptIndex = index;

      listItem.innerHTML = `
          <div>
              <span>${prompt.name}</span>
              <span class="badge badge-secondary ml-2">${prompt.model}</span>
          </div>
          <div>
              <button class="btn btn-sm btn-secondary move-up"><i class="fas fa-arrow-up"></i></button>
              <button class="btn btn-sm btn-secondary move-down"><i class="fas fa-arrow-down"></i></button>
              <button class="btn btn-sm btn-danger remove-prompt"><i class="fas fa-times"></i></button>
          </div>
      `;
      selectedPromptsList.appendChild(listItem);
  });

  // Attach event listeners for buttons
  selectedPromptsList.querySelectorAll('.move-up').forEach((btn) => {
      btn.addEventListener('click', function () {
          const li = this.closest('li');
          if (li.previousElementSibling) {
              li.parentNode.insertBefore(li, li.previousElementSibling);
              updateMultipromptFlowVisualization();
              saveHomeSettingsHome();
          }
      });
  });

  selectedPromptsList.querySelectorAll('.move-down').forEach((btn) => {
      btn.addEventListener('click', function () {
          const li = this.closest('li');
          if (li.nextElementSibling) {
              li.parentNode.insertBefore(li.nextElementSibling, li);
              updateMultipromptFlowVisualization();
              saveHomeSettingsHome();
          }
      });
  });

  selectedPromptsList.querySelectorAll('.remove-prompt').forEach((btn) => {
      btn.addEventListener('click', function () {
          const li = this.closest('li');
          li.parentNode.removeChild(li);
          // Update Select2 selection
          const updatedIndices = Array.from(selectedPromptsList.children).map(
              (item) => item.dataset.promptIndex
          );
          $('#fuuga_multiprompt-select').val(updatedIndices).trigger('change');
          updateMultipromptFlowVisualization();
          saveHomeSettingsHome();
      });
  });

  // Update visualization
  updateMultipromptFlowVisualization();
}

function updateMultipromptFlowVisualization() {
  const selectedPromptsList = document.getElementById('fuuga_selected-prompts-list');
  const prompts = getPrompts();

  const items = selectedPromptsList.querySelectorAll('li');
  const indices = Array.from(items).map((item) => item.dataset.promptIndex);

  const multipromptFlowDiv = document.getElementById('fuuga_multiprompt-flow');
  multipromptFlowDiv.innerHTML = '';

  // Accordion for flow visualization
  const accordionId = 'fuuga_multiprompt-flow-accordion';
  let accordionHTML = `<div class="accordion" id="${accordionId}">`;

  // Original text panel
  accordionHTML += `
      <div class="card">
          <div class="card-header" id="${accordionId}-heading-original">
              <h2 class="mb-0">
                  <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#${accordionId}-collapse-original" aria-expanded="true" aria-controls="${accordionId}-collapse-original">
                      <i class="fas fa-file-alt"></i>  
                  </button>
              </h2>
          </div>
          <div id="${accordionId}-collapse-original" class="collapse" aria-labelledby="${accordionId}-heading-original" data-parent="#${accordionId}">
              <div class="card-body" id="fuuga_multiprompt-original-text">
                       .
              </div>
          </div>
      </div>
  `;

  indices.forEach((index, idx) => {
      const prompt = prompts[index];
      const collapseId = `${accordionId}-collapse-${idx}`;
      accordionHTML += `
          <div class="card">
              <div class="card-header" id="${accordionId}-heading-${idx}">
                  <h2 class="mb-0">
                      <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#${collapseId}" aria-expanded="true" aria-controls="${collapseId}">
                          <i class="fas fa-bolt"></i>  ${idx + 1}: ${prompt.name}
                      </button>
                  </h2>
              </div>
              <div id="${collapseId}" class="collapse" aria-labelledby="${accordionId}-heading-${idx}" data-parent="#${accordionId}">
                  <div class="card-body">
                      <p><strong> :</strong> ${prompt.text || ' '}</p>
                      <p><strong>:</strong> ${prompt.model}</p>
                      <p><strong>        .</strong></p>
                      <div id="fuuga_multiprompt-step-result-${idx}" class="mt-2"></div>
                  </div>
              </div>
          </div>
      `;
  });

  // Formatting step
  const mainSettings = JSON.parse(localStorage.getItem('mainSettings') || '{}');
  const applyFormatting = mainSettings.applyFormatting || false;
  if (applyFormatting) {
      accordionHTML += `
          <div class="card">
              <div class="card-header" id="${accordionId}-heading-formatting">
                  <h2 class="mb-0">
                      <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#${accordionId}-collapse-formatting" aria-expanded="true" aria-controls="${accordionId}-collapse-formatting">
                          <i class="fas fa-paint-brush"></i>  
                      </button>
                  </h2>
              </div>
              <div id="${accordionId}-collapse-formatting" class="collapse" aria-labelledby="${accordionId}-heading-formatting" data-parent="#${accordionId}">
                  <div class="card-body">
                      <p>        .</p>
                  </div>
              </div>
          </div>
      `;
  }

  accordionHTML += '</div>';

  multipromptFlowDiv.innerHTML = accordionHTML;

  // Re-initialize Bootstrap collapse for dynamically added elements
  $('.collapse').collapse();
}

