function cancelProcess() {
  if (window.abortController) {
      window.abortController.abort();
      showLoadingHome(false);
      window.abortController = null;
      document.getElementById('fuuga_process-text').disabled = false;
      cleanAllTimestampsHome();
  }
}

async function cleanAllTimestampsHome() {
  try {
      await Word.run(async (context) => {
          const body = context.document.body;

          async function removeMarkers(prefix) {
              const searchPattern = `${prefix}*`;
              const searchResults = body.search(searchPattern, { matchWildcards: true });
              searchResults.load("items");
              await context.sync();

              for (let range of searchResults.items) {
                  range.delete();
              }

              await context.sync();
          }

          await removeMarkers("START_TIMESTAMP");
          await removeMarkers("END_TIMESTAMP");

          console.log("  timestamp  .");
      });
  } catch (error) {
      console.error('   :', error);
  }
}

async function createSelectionMarkersHome() {
  try {
      return await Word.run(async (context) => {
          const selection = context.document.getSelection();
          selection.load("text");
          await context.sync();

          const selectionText = selection.text.trim();
          if (selectionText.length === 0) {
              throw new Error("  ");
          }

          const startMarker = "START_TIMESTAMP";
          const endMarker = "END_TIMESTAMP";

          selection.insertText(startMarker, Word.InsertLocation.before);
          selection.insertText(endMarker, Word.InsertLocation.after);

          const startRange = context.document.body.search(startMarker, { matchCase: true }).getFirst();
          const endRange = context.document.body.search(endMarker, { matchCase: true }).getFirst();

          startRange.font.size = 1;
          endRange.font.size = 1;
          startRange.font.color = "white";
          endRange.font.color = "white";

          await context.sync();

          return {
              text: selectionText
          };
      });
  } catch (error) {
      console.error('    :', error);
      throw error;
  }
}


