function splitTextIntoChunksHome(text, chunkSize) {
  const sentences = text.match(/[^.!?]+[.!?]+(\s|$)|[^.!?]+$/g) || [text];
  const chunks = [];
  for (let i = 0; i < sentences.length; i += chunkSize) {
      const chunk = sentences
          .slice(i, i + chunkSize)
          .join(' ')
          .trim();
      chunks.push(chunk);
  }
  return chunks;
}

async function processChunkWithRetryHome(
  text,
  temperature,
  maxTokens,
  signal,
  applyFormatting,
  formattingModel,
  formattingTemperature,
  formattingMaxTokens,
  prompt,
  enableLogger,
  loggerLevel,
  loggerModel,
  loggerTemperature,
  loggerMaxTokens,
  customLoggerPrompt,
  loggerPrompts,
  retryCount = 1,
  isSinglePrompt = false
) {
  let attempts = 0;

  while (attempts < retryCount) {
      try {
          const result = await processChunkHome(
              text,
              temperature,
              maxTokens,
              signal,
              applyFormatting,
              formattingModel,
              formattingTemperature,
              formattingMaxTokens,
              prompt,
              enableLogger,
              loggerLevel,
              loggerModel,
              loggerTemperature,
              loggerMaxTokens,
              customLoggerPrompt,
              loggerPrompts,
              isSinglePrompt
          );

          // ,      
          return {
              generatedText: result.generatedText || '',
              formattingInfo: result.formattingInfo || [],
              loggerOutput: result.loggerOutput || null
          };
      } catch (error) {
          if (error.name === 'AbortError') {
              throw error;
          } else {
              attempts++;
              if (attempts >= retryCount) {
                  throw error;
              } else {
                  console.warn(` ${attempts}  . ...`);
              }
          }
      }
  }

  //     ,     
  return {
      generatedText: '',
      formattingInfo: [],
      loggerOutput: null
  };
}


async function processChunkHome(
  text,
  temperature,
  maxTokens,
  signal,
  applyFormatting,
  formattingModel,
  formattingTemperature,
  formattingMaxTokens,
  prompt,
  enableLogger,
  loggerLevel,
  loggerModel,
  loggerTemperature,
  loggerMaxTokens,
  customLoggerPrompt,
  loggerPrompts,
  isSinglePrompt
) {
  const apiKey = localStorage.getItem('apiKey');

  const promptModel = prompt.model;
  const promptText = prompt.text;

  // Build system and user prompts
  const systemPrompt = ` - AI    .   -        .     ,   .    .`;

  const userPrompt = `${promptText}\n\n   : "${text}"`;

  const requestBody = {
      model: promptModel,
      messages: [
          { role: 'system', content: systemPrompt },
          { role: 'user', content: userPrompt },
      ],
      temperature: temperature,
      max_tokens: maxTokens,
  };

  try {
      const response = await fetch('https://openrouter.ai/api/v1/chat/completions', {
          method: 'POST',
          headers: {
              Authorization: `Bearer ${apiKey}`,
              'Content-Type': 'application/json',
              'HTTP-Referer': 'https://www.office.com',
              'X-Title': 'Word Text Processor',
          },
          body: JSON.stringify(requestBody),
          signal: signal,
      });

      if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      console.log('  :', data.choices[0].message.content.trim());

      let generatedText = data.choices[0].message.content.trim();

      let formattingInfo = [];

      if (applyFormatting && isSinglePrompt) {
          const currentStepSpinner = document.getElementById('fuuga_current-step-spinner');
          currentStepSpinner.innerText = ' ';

          // Get original formatting info
          const originalFormattingInfo = await Word.run(async (context) => {
              const selection = context.document.getSelection();
              return await getWordsInfo(context, selection);
          });

          // Generate updated formatting
          formattingInfo = await getUpdatedFormattingInfo(
              originalFormattingInfo,
              text,
              generatedText,
              formattingModel,
              formattingTemperature,
              formattingMaxTokens,
              signal
          );
      }

      // Logger processing
      let loggerOutput = null;
      if (enableLogger) {
          loggerOutput = await processLogger(
              text,
              generatedText,
              prompt,
              signal,
              loggerLevel,
              isSinglePrompt ? 'Single Prompt' : 'Chunk Processing',
              loggerModel,
              loggerTemperature,
              loggerMaxTokens,
              customLoggerPrompt,
              loggerPrompts
          );
      }

      return { generatedText, formattingInfo, loggerOutput };
  } catch (error) {
      if (error.name === 'AbortError') {
          console.log('  .');
          throw error;
      } else {
          console.error('Error in processChunkHome:', error);
          throw error;
      }
  }
}


