async function processLogger(
  beforeText,
  afterText,
  prompt,
  signal,
  loggerLevel,
  stepDescription,
  loggerModel,
  loggerTemperature,
  loggerMaxTokens,
  customLoggerPrompt
) {
  const apiKey = localStorage.getItem('apiKey');

  const mainSettings = JSON.parse(localStorage.getItem('mainSettings') || '{}');
  const loggerPrompts = mainSettings.loggerPrompts || getDefaultLoggerPromptsHome();

  let loggerPromptText = '';

  if (loggerLevel === 'custom' && customLoggerPrompt) {
      loggerPromptText = customLoggerPrompt
          .replace('{{beforeText}}', beforeText)
          .replace('{{afterText}}', afterText);
  } else {
      loggerPromptText = loggerPrompts[loggerLevel] || '       .';
  }

  const systemPrompt = ` - AI     .   -           .
  
       :

  1.  :
  {
      "level": "minimal",
      "message": "    ."
  }

  2.  :
  {
      "level": "medium",
      "message": "  ,   .      ."
  }

  3.  :
  {
      "level": "maximal",
      "message": "      . ,      .      : ' '   ''.      .",
      "details": "  ...",
      "examples": [" 1", " 2"]
  }

                .    .     JSON      .
  `;

  const userPrompt =
      loggerLevel === 'custom' && customLoggerPrompt
          ? loggerPromptText
          : `${loggerPromptText}\n\n: "${prompt.name}"\n\n  : "${beforeText}"\n\n  : "${afterText}"`;

  const requestBody = {
      model: loggerModel,
      messages: [
          { role: 'system', content: systemPrompt },
          { role: 'user', content: userPrompt },
      ],
      temperature: loggerTemperature,
      max_tokens: loggerMaxTokens,
  };

  try {
      const response = await fetch('https://openrouter.ai/api/v1/chat/completions', {
          method: 'POST',
          headers: {
              Authorization: `Bearer ${apiKey}`,
              'Content-Type': 'application/json',
              'HTTP-Referer': 'https://www.office.com',
              'X-Title': 'Word Logger',
          },
          body: JSON.stringify(requestBody),
          signal: signal,
      });

      if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      console.log('   ():', data.choices[0].message.content.trim());

      let loggerOutputRaw = data.choices[0].message.content.trim();

      //  JSON 
      let loggerOutput;
      try {
          loggerOutput = JSON.parse(loggerOutputRaw);
      } catch (parseError) {
          console.error('   JSON  :', parseError);
          return null;
      }

      //   JSON
      if (!loggerOutput.level || !loggerOutput.message) {
          console.error('  JSON  .');
          return null;
      }

      return {
          stepDescription,
          promptName: prompt.name,
          loggerOutput,
      };
  } catch (error) {
      if (error.name === 'AbortError') {
          console.log('  .');
          return null;
      } else {
          console.error('Error in processLogger:', error);
          return null;
      }
  }
}


function displayLoggerOutputs(loggerOutputs, containerId) {
  const loggerDiv = document.getElementById(containerId);
  loggerDiv.innerHTML = '';

  const accordionId = `${containerId}-accordion`;

  let accordionHTML = `<div class="accordion" id="${accordionId}">`;

  loggerOutputs.forEach((log, index) => {
      const collapseId = `${accordionId}-collapse-${index}`;
      const headerId = `${accordionId}-heading-${index}`;

      //        
      let headerClass = '';
      let iconClass = '';
      let cardHeaderContent = '';

      switch (log.loggerOutput.level) {
          case 'minimal':
              headerClass = 'bg-info text-white';
              iconClass = 'fas fa-info-circle';
              break;
          case 'medium':
              headerClass = 'bg-warning text-dark';
              iconClass = 'fas fa-exclamation-triangle';
              break;
          case 'maximal':
              headerClass = 'bg-danger text-white';
              iconClass = 'fas fa-exclamation-circle';
              break;
          default:
              headerClass = 'bg-secondary text-white';
              iconClass = 'fas fa-question-circle';
      }

      //   
      const formattedLog = formatLoggerOutput(log.loggerOutput);

      accordionHTML += `
          <div class="card">
              <div class="card-header ${headerClass}" id="${headerId}">
                  <h2 class="mb-0">
                      <button class="btn btn-link text-white" type="button" data-toggle="collapse" data-target="#${collapseId}" aria-expanded="${index === 0 ? 'true' : 'false'}" aria-controls="${collapseId}">
                          <i class="${iconClass}"></i> ${log.stepDescription}: ${log.promptName}
                      </button>
                  </h2>
              </div>
              <div id="${collapseId}" class="collapse ${index === 0 ? 'show' : ''}" aria-labelledby="${headerId}" data-parent="#${accordionId}">
                  <div class="card-body">
                      ${formattedLog}
                  </div>
              </div>
          </div>
      `;
  });

  accordionHTML += '</div>';

  loggerDiv.innerHTML = accordionHTML;
}

function formatLoggerOutput(loggerOutput) {
  let html = `
      <p><strong>:</strong> ${capitalizeFirstLetter(loggerOutput.level)}</p>
      <p><strong>:</strong> ${loggerOutput.message}</p>
  `;

  if (loggerOutput.level === 'medium') {
      if (loggerOutput.details) {
          html += `<p><strong>:</strong> ${loggerOutput.details}</p>`;
      }
  }

  if (loggerOutput.level === 'maximal') {
      if (loggerOutput.details) {
          html += `<p><strong>:</strong> ${loggerOutput.details}</p>`;
      }
      if (loggerOutput.examples && Array.isArray(loggerOutput.examples)) {
          html += `<p><strong>:</strong></p><ul>`;
          loggerOutput.examples.forEach(example => {
              html += `<li>${example}</li>`;
          });
          html += `</ul>`;
      }
  }

  return html;
}

function capitalizeFirstLetter(string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}


function showLoadingHome(isLoading, useProgressBar = false) {
  const loadingElement = document.getElementById('fuuga_loading');
  const processButton = document.getElementById('fuuga_process-text');
  const cancelButton = document.getElementById('fuuga_cancel-process');
  const progressContainer = document.getElementById('fuuga_progress-container');
  const spinner = document.getElementById('fuuga_spinner');
  if (isLoading) {
      loadingElement.style.display = 'block';
      processButton.disabled = true;
      cancelButton.style.display = 'inline-block';
      if (useProgressBar) {
          progressContainer.style.display = 'block';
          spinner.style.display = 'none';
      } else {
          progressContainer.style.display = 'none';
          spinner.style.display = 'block';
      }
  } else {
      loadingElement.style.display = 'none';
      processButton.disabled = false;
      cancelButton.style.display = 'none';
      progressContainer.style.display = 'none';
      spinner.style.display = 'none';
  }
}

