// Preset Functions
function initializePresetSelect() {
  $('#fuuga_preset-select').select2({
      width: '100%',
      placeholder: ' ',
  });
}

function populatePresetSelect() {
  const select = $('#fuuga_preset-select');
  select.empty();

  // Add "Current Settings" option
  const currentOption = new Option(' ', 'current', false, false);
  select.append(currentOption);

  let presets = JSON.parse(localStorage.getItem('homePresets') || '[]');
  let lastSelectedPreset = localStorage.getItem('homeLastSelectedPreset') || null;

  presets.forEach(preset => {
      const option = new Option(preset.name, preset.name, false, false);
      select.append(option);
  });

  if (lastSelectedPreset && lastSelectedPreset !== 'current') {
      select.val(lastSelectedPreset).trigger('change');
  } else {
      // Set "Current Settings" if no last selected preset
      select.val('current').trigger('change');
  }
}

function loadSelectedPreset() {
  window.isLoadingPreset = true;

  const presetName = $('#fuuga_preset-select').val();
  if (!presetName || presetName === 'current') {
      // If "Current Settings" is selected, do nothing
      window.isLoadingPreset = false;
      return;
  }

  let presets = JSON.parse(localStorage.getItem('homePresets') || '[]');
  const preset = presets.find(p => p.name === presetName);
  if (!preset) {
      window.isLoadingPreset = false;
      return;
  }

  // Load settings from preset
  const settings = preset.settings;
  document.getElementById('fuuga_apply-formatting').checked = settings.applyFormatting;
  document.getElementById('fuuga_split-into-chunks-checkbox').checked = settings.splitIntoChunks;
  document.getElementById('fuuga_chunk-size').value = settings.chunkSize;
  document.getElementById('fuuga_max-chars').value = settings.maxChars;
  document.getElementById('fuuga_temperature').value = settings.temperature;
  document.getElementById('fuuga_max-tokens').value = settings.maxTokens;
  document.getElementById('fuuga_formatting-temperature').value = settings.formattingTemperature;
  document.getElementById('fuuga_formatting-max-tokens').value = settings.formattingMaxTokens;
  document.getElementById('fuuga_enable-logger').checked = settings.enableLogger;
  document.getElementById('fuuga_logger-level').value = settings.loggerLevel;
  document.getElementById('fuuga_custom-logger-prompt').value = settings.customLoggerPrompt;
  document.getElementById('fuuga_logger-temperature').value = settings.loggerTemperature;
  document.getElementById('fuuga_logger-max-tokens').value = settings.loggerMaxTokens;
  document.getElementById('fuuga_edit-logger-prompts').checked = settings.editLoggerPrompts;

  $('#fuuga_formatting-model').val(settings.formattingModel).trigger('change');
  $('#fuuga_logger-model').val(settings.loggerModel).trigger('change');

  // Load logger prompts
  document.getElementById('fuuga_logger-prompt-minimal').value =
      settings.loggerPrompts.minimal || '';
  document.getElementById('fuuga_logger-prompt-medium').value =
      settings.loggerPrompts.medium || '';
  document.getElementById('fuuga_logger-prompt-maximal').value =
      settings.loggerPrompts.maximal || '';

  // Show or hide custom logger prompt container
  if (settings.loggerLevel === 'custom') {
      document.getElementById('fuuga_custom-logger-prompt-container').style.display = 'block';
  } else {
      document.getElementById('fuuga_custom-logger-prompt-container').style.display = 'none';
  }

  // Show or hide logger prompts accordion
  const editLoggerPrompts = document.getElementById('fuuga_edit-logger-prompts').checked;
  const loggerPromptsAccordion = document.getElementById('fuuga_logger-prompts-accordion');
  loggerPromptsAccordion.style.display = editLoggerPrompts ? 'block' : 'none';

  // Save settings to localStorage
  saveHomeSettingsHome();

  // Set last selected preset
  localStorage.setItem('homeLastSelectedPreset', presetName);

  // Update preset JSON
  updatePresetJSON();

  window.isLoadingPreset = false;
}

function saveCurrentSettingsAsPreset() {
  const presetNameInput = document.getElementById('fuuga_preset-name');
  const presetName = presetNameInput.value.trim();
  if (!presetName) {
      displayPresetMessage('     .', 'danger');
      return;
  }

  if (presetName.toLowerCase() === 'current' || presetName === ' ') {
      displayPresetMessage('     " ".', 'danger');
      return;
  }

  const preset = {
      name: presetName,
      settings: getCurrentSettings(),
  };

  let presets = JSON.parse(localStorage.getItem('homePresets') || '[]');

  // Check if preset with same name exists
  const existingIndex = presets.findIndex(p => p.name === presetName);
  if (existingIndex !== -1) {
      // Offer to overwrite existing preset
      showPresetConfirmation(`   "${presetName}"  .  ?`, () => {
          presets[existingIndex] = preset;
          localStorage.setItem('homePresets', JSON.stringify(presets));
          populatePresetSelect();

          // Select the newly created preset
          $('#fuuga_preset-select').val(preset.name).trigger('change');
          localStorage.setItem('homeLastSelectedPreset', preset.name);

          displayPresetMessage(' .', 'success');

          // Clear preset name input
          presetNameInput.value = '';

          // Update preset JSON
          updatePresetJSON();
      });
  } else {
      presets.push(preset);
      localStorage.setItem('homePresets', JSON.stringify(presets));
      populatePresetSelect();

      // Select the newly created preset
      $('#fuuga_preset-select').val(preset.name).trigger('change');
      localStorage.setItem('homeLastSelectedPreset', preset.name);

      displayPresetMessage(' .', 'success');

      // Clear preset name input
      presetNameInput.value = '';

      // Update preset JSON
      updatePresetJSON();
  }
}

function deleteSelectedPreset() {
  const presetName = $('#fuuga_preset-select').val();
  if (!presetName || presetName === 'current') {
      displayPresetMessage('    .', 'danger');
      return;
  }

  showPresetConfirmation(` ,     "${presetName}"?`, () => {
      let presets = JSON.parse(localStorage.getItem('homePresets') || '[]');
      presets = presets.filter(p => p.name !== presetName);
      localStorage.setItem('homePresets', JSON.stringify(presets));

      // Reset preset selection
      $('#fuuga_preset-select').val('current').trigger('change');
      localStorage.removeItem('homeLastSelectedPreset');

      populatePresetSelect();

      displayPresetMessage(' .', 'success');

      // Clear JSON field
      document.getElementById('fuuga_preset-json').value = '';
  });
}

function updatePresetJSON() {
  const presetName = $('#fuuga_preset-select').val();
  if (!presetName || presetName === 'current') {
      document.getElementById('fuuga_preset-json').value = '';
      return;
  }

  let presets = JSON.parse(localStorage.getItem('homePresets') || '[]');
  const preset = presets.find(p => p.name === presetName);
  if (!preset) {
      document.getElementById('fuuga_preset-json').value = '';
      return;
  }

  const presetJSON = JSON.stringify(preset, null, 2);
  document.getElementById('fuuga_preset-json').value = presetJSON;
}

function getDefaultLoggerPromptsHome() {
  return {
      minimal: '        .',
      medium: '       ,   .',
      maximal: '         ,   .',
  };
}



function copyPresetJSON() {
  const presetJSONTextarea = document.getElementById('fuuga_preset-json');
  presetJSONTextarea.select();
  document.execCommand('copy');
  displayPresetMessage('JSON     .', 'success');
}

function importPresetFromJSONHome() {
  const presetJSON = document.getElementById('fuuga_preset-json-import').value;
  if (!presetJSON) {
      displayPresetMessage(' JSON .', 'danger');
      return;
  }

  try {
      const preset = JSON.parse(presetJSON);
      if (!preset.name || !preset.settings) {
          displayPresetMessage('  .', 'danger');
          return;
      }

      if (preset.name.toLowerCase() === 'current' || preset.name === ' ') {
          displayPresetMessage('     " ".', 'danger');
          return;
      }

      let presets = JSON.parse(localStorage.getItem('homePresets') || '[]');

      // Check if preset with same name exists
      const existingIndex = presets.findIndex(p => p.name === preset.name);
      if (existingIndex !== -1) {
          showPresetConfirmation(`   "${preset.name}"  .  ?`, () => {
              presets[existingIndex] = preset;
              localStorage.setItem('homePresets', JSON.stringify(presets));
              populatePresetSelect();
              $('#fuuga_preset-select').val(preset.name).trigger('change');
              localStorage.setItem('homeLastSelectedPreset', preset.name);

              displayPresetMessage(' .', 'success');
              updatePresetJSON();
          });
      } else {
          presets.push(preset);
          localStorage.setItem('homePresets', JSON.stringify(presets));
          populatePresetSelect();
          $('#fuuga_preset-select').val(preset.name).trigger('change');
          localStorage.setItem('homeLastSelectedPreset', preset.name);

          displayPresetMessage(' .', 'success');
          updatePresetJSON();
      }
  } catch (error) {
      console.error('   :', error);
      displayPresetMessage('   .   JSON.', 'danger');
  }
}

function getCurrentSettings() {
  const mainSettings = JSON.parse(localStorage.getItem('mainSettings') || '{}');

  // Do not include lastSelectedPromptIndex and lastSelectedMultipromptIndices in presets
  const { lastSelectedPromptIndex, lastSelectedMultipromptIndices, ...settings } = mainSettings;

  return settings;
}

function displayPresetMessage(message, type) {
  const messageDiv = document.getElementById('fuuga_preset-message');
  messageDiv.innerHTML = `<div class="alert alert-${type}">${message}</div>`;
  setTimeout(() => {
      messageDiv.innerHTML = '';
  }, 3000);
}

function showPresetConfirmation(message, onConfirm) {
  const confirmationDiv = document.getElementById('fuuga_preset-confirmation');
  const confirmationMessage = document.getElementById('fuuga_preset-confirmation-message');
  const confirmYesButton = document.getElementById('fuuga_preset-confirm-yes');
  const confirmNoButton = document.getElementById('fuuga_preset-confirm-no');

  confirmationMessage.textContent = message;
  confirmationDiv.style.display = 'block';

  // Remove previous event listeners
  confirmYesButton.onclick = null;
  confirmNoButton.onclick = null;

  confirmYesButton.onclick = () => {
      confirmationDiv.style.display = 'none';
      onConfirm();
  };

  confirmNoButton.onclick = () => {
      confirmationDiv.style.display = 'none';
  };
}

// Additional code to fix the Tools tab issue
 async function showSelectedTextInfoHome() {
  try {
      await Word.run(async (context) => {
          const selection = context.document.getSelection();
          const wordsInfo = await getWordsInfo(context, selection);

          if (wordsInfo.length > 0) {
              //   
              const colorCounts = {};
              const fontCounts = {};
              const sizeCounts = {};

              wordsInfo.forEach(word => {
                  const color = word.color || 'default';
                  const font = word.fontName || 'default';
                  const size = word.size || 'default';

                  colorCounts[color] = (colorCounts[color] || 0) + 1;
                  fontCounts[font] = (fontCounts[font] || 0) + 1;
                  sizeCounts[size] = (sizeCounts[size] || 0) + 1;
              });

              const baseColor = getMostCommonValueHome(colorCounts);
              const baseFont = getMostCommonValueHome(fontCounts);
              const baseSize = getMostCommonValueHome(sizeCounts);

              displayWordsInfo(wordsInfo, baseColor, baseFont, baseSize);
          } else {
              const textInfoDiv = document.getElementById('fuuga_selected-text-info');
              textInfoDiv.innerHTML = '<p class="text-warning">,     .</p>';
          }
      });
  } catch (error) {
      console.error('Error in showSelectedTextInfo:', error);
      const textInfoDiv = document.getElementById('fuuga_selected-text-info');
      textInfoDiv.innerHTML = '<p class="text-danger">       . ,   .</p>';
  }
}

function getMostCommonValueHome(counts) {
  return Object.entries(counts).reduce((a, b) => a[1] >= b[1] ? a : b)[0];
}






