function showAlertPrompt(message) {
    $('#crocodile_alertModalBody').text(message);
    $('#crocodile_alertModal').modal('show');
}

//    
function showConfirm(message) {
    return new Promise((resolve, reject) => {
        $('#crocodile_confirmModalBody').text(message);
        $('#crocodile_confirmModal').modal('show');
        $('#crocodile_confirmModalOk').off('click').on('click', () => {
            $('#crocodile_confirmModal').modal('hide');
            resolve(true);
        });
        $('#crocodile_confirmModalCancel').off('click').on('click', () => {
            $('#crocodile_confirmModal').modal('hide');
            resolve(false);
        });
        //    
        $('#crocodile_confirmModal').off('hidden.bs.modal').on('hidden.bs.modal', () => {
            resolve(false);
        });
    });
}

function populateModelSelectPrompt() {
    const select = $('#crocodile_prompt-model-select');
    select.empty();
    const models = JSON.parse(localStorage.getItem('models') || '[]');
    models.forEach(model => {
        const option = new Option(model.name, model.id, false, false);
        select.append(option);
    });
    select.trigger('change');
}

function populateTagsFilter() {
    const selectFilter = $('#crocodile_filter-tags-select');
    const selectPromptTags = $('#crocodile_prompt-tags-input');
    selectFilter.empty();
    selectPromptTags.empty();

    const prompts = getPrompts();
    const tagsMap = new Map();
    prompts.forEach(prompt => {
        if (prompt.tags) {
            prompt.tags.forEach(tag => {
                if (tagsMap.has(tag)) {
                    tagsMap.set(tag, tagsMap.get(tag) + 1);
                } else {
                    tagsMap.set(tag, 1);
                }
            });
        }
    });

    const tags = Array.from(tagsMap.entries()).sort();

    tags.forEach(([tag, count]) => {
        const optionText = `${tag} (${count})`;
        const optionFilter = new Option(optionText, tag, false, false);
        const optionPromptTag = new Option(tag, tag, false, false);
        selectFilter.append(optionFilter);
        selectPromptTags.append(optionPromptTag);
    });

    selectFilter.trigger('change');
    selectPromptTags.trigger('change');
}

function populatePresetSelectPrompts() {
    const select = $('#crocodile_preset-select-prompts');
    select.empty();
    const presets = JSON.parse(localStorage.getItem('promptPresetsAkula') || '[]');
    if (presets.length === 0) {
        select.append(new Option('  ', '', false, false));
    } else {
        select.append(new Option(' ', '', false, false));
        presets.forEach(preset => {
            select.append(new Option(preset.name, preset.name, false, false));
        });
    }
    select.trigger('change');
}

