async function savePrompt() {
    const name = document.getElementById('crocodile_prompt-name-input').value.trim();
    const model = $('#crocodile_prompt-model-select').val();
    const text = document.getElementById('crocodile_prompt-textarea-input').value.trim();
    let tags = $('#crocodile_prompt-tags-input').val();

    if (name && model && text) {
        const now = new Date();
        const prompts = getPrompts();
        //        
        const existingIndex = prompts.findIndex(p => p.name === name);
        if (existingIndex !== -1) {
            const confirmOverwrite = await showConfirm(`   "${name}"  .  ?`);
            if (!confirmOverwrite) {
                return;
            }
            prompts[existingIndex] = {
                name,
                model,
                text,
                tags,
                createdAt: prompts[existingIndex].createdAt || now.toISOString(),
                updatedAt: now.toISOString()
            };
        } else {
            prompts.push({
                name,
                model,
                text,
                tags,
                createdAt: now.toISOString(),
                updatedAt: now.toISOString()
            });
        }
        savePrompts(prompts);
        updatePromptList();
        updatePromptSelect();
        populateTagsFilter(); //        
        document.getElementById('crocodile_prompt-name-input').value = '';
        $('#crocodile_prompt-model-select').val(null).trigger('change');
        document.getElementById('crocodile_prompt-textarea-input').value = '';
        $('#crocodile_prompt-tags-input').val(null).trigger('change');

        showAlertPrompt('  .');
    } else {
        showAlertPrompt(',   .');
    }
}

function updatePromptList() {
    const listContainer = document.getElementById('crocodile_prompt-list');
    const prompts = getPrompts();
    const filterTags = $('#crocodile_filter-tags-select').val() || [];
    const sortBy = $('#crocodile_sort-prompts-select').val() || 'name_asc';
    listContainer.innerHTML = '';

    //   
    const now = new Date().toISOString();
    prompts.forEach(prompt => {
        if (!prompt.createdAt) {
            prompt.createdAt = now;
        }
        if (!prompt.updatedAt) {
            prompt.updatedAt = prompt.createdAt;
        }
    });
    savePrompts(prompts);

    //        
    let filteredPrompts = prompts.map((prompt, index) => ({ prompt, originalIndex: index }));

    //   
    if (filterTags.length > 0) {
        filteredPrompts = filteredPrompts.filter(({ prompt }) => {
            if (prompt.tags && prompt.tags.length > 0) {
                return filterTags.every(tag => prompt.tags.includes(tag));
            }
            return false;
        });
    }

    // 
    filteredPrompts.sort((a, b) => {
        const promptA = a.prompt;
        const promptB = b.prompt;
        switch (sortBy) {
            case 'name_asc':
                return promptA.name.localeCompare(promptB.name);
            case 'name_desc':
                return promptB.name.localeCompare(promptA.name);
            case 'length_asc':
                return promptA.text.length - promptB.text.length;
            case 'length_desc':
                return promptB.text.length - promptA.text.length;
            case 'model_asc':
                return promptA.model.localeCompare(promptB.model);
            case 'model_desc':
                return promptB.model.localeCompare(promptA.model);
            default:
                return 0;
        }
    });

    if (filteredPrompts.length === 0) {
        listContainer.innerHTML = '<p class="text-muted">  .</p>';
        return;
    }

    //     
    filteredPrompts.forEach(({ prompt, originalIndex }) => {
        const card = createPromptCard(prompt, originalIndex, {
            buttons: [
                { text: '', className: 'btn-primary edit-prompt', data: { index: originalIndex } },
                { text: '', className: 'btn-info duplicate-prompt', data: { index: originalIndex } },
                { text: '', className: 'btn-danger delete-prompt', data: { index: originalIndex } },
            ],
            eventHandlers: {
                '.edit-prompt': (e) => editPrompt(e.target.dataset.index),
                '.duplicate-prompt': (e) => duplicatePrompt(e.target.dataset.index),
                '.delete-prompt': (e) => deletePrompt(e.target.dataset.index),
            },
        });
        listContainer.appendChild(card);
    });
}


function createPromptCard(prompt, index, options = {}) {
    const card = document.createElement('div');
    card.className = 'card mt-2';
    const promptLength = prompt.text.length;
    const createdAt = new Date(prompt.createdAt).toLocaleString();
    const updatedAt = prompt.updatedAt ? new Date(prompt.updatedAt).toLocaleString() : createdAt;

    let dateInfo = `<small class="text-muted">: ${createdAt}`;
    if (prompt.updatedAt && prompt.updatedAt !== prompt.createdAt) {
        dateInfo += ` | : ${updatedAt}`;
    }
    dateInfo += '</small>';

    let buttonsHTML = '';
    if (options.buttons && options.buttons.length > 0) {
        buttonsHTML = options.buttons.map(btn => {
            const dataAttributes = Object.entries(btn.data || {}).map(([key, value]) => `data-${key}="${value}"`).join(' ');
            return `<button class="btn btn-sm ${btn.className} mr-2" ${dataAttributes}>${btn.text}</button>`;
        }).join('');
    }

    card.innerHTML = `
        <div class="card-body">
            <p><strong>:</strong> ${prompt.name}</p>
            <p><strong>:</strong> ${prompt.model}</p>
            <p><strong> (${promptLength}):</strong> ${prompt.text}</p>
            ${prompt.tags && prompt.tags.length > 0 ? `<p><strong>:</strong> ${prompt.tags.join(', ')}</p>` : ''}
            <p>${dateInfo}</p>
            ${buttonsHTML}
        </div>
    `;

    // Now attach event handlers if needed
    if (options.eventHandlers) {
        Object.entries(options.eventHandlers).forEach(([selector, handler]) => {
            const elements = card.querySelectorAll(selector);
            elements.forEach(element => {
                element.addEventListener('click', handler);
            });
        });
    }
    return card;
}

async function deletePrompt(index) {
    const confirmDelete = await showConfirm(' ,     ?');
    if (!confirmDelete) {
        return;
    }
    const prompts = getPrompts();
    prompts.splice(index, 1);
    savePrompts(prompts);
    updatePromptList();
    updatePromptSelect();
    populateTagsFilter(); //     
    showAlertPrompt(' .');
}

function editPrompt(index) {
    index = Number(index); //   
    const prompts = getPrompts();
    const prompt = prompts[index];
    document.getElementById('crocodile_prompt-name-input').value = prompt.name;
    $('#crocodile_prompt-model-select').val(prompt.model).trigger('change');
    document.getElementById('crocodile_prompt-textarea-input').value = prompt.text;
    $('#crocodile_prompt-tags-input').val(prompt.tags).trigger('change');
    
    const saveButton = document.getElementById('crocodile_save-prompt-btn');
    saveButton.textContent = ' ';
    saveButton.onclick = () => {
        updatePrompt(index);
        saveButton.textContent = ' ';
        saveButton.onclick = savePrompt;
        
        //   " "
        document.getElementById('crocodile_cancel-edit-btn').style.display = 'none';
    };
    
    //   " "
    document.getElementById('crocodile_cancel-edit-btn').style.display = 'inline-block';
    
    //    ""
    $('#crocodile_promptsSubTab a[href="#crocodile_createPromptTabContent"]').tab('show');
}

async function updatePrompt(index) {
    const name = document.getElementById('crocodile_prompt-name-input').value.trim();
    const model = $('#crocodile_prompt-model-select').val();
    const text = document.getElementById('crocodile_prompt-textarea-input').value.trim();
    const tags = $('#crocodile_prompt-tags-input').val();

    if (name && model && text) {
        const now = new Date();
        const prompts = getPrompts();
        prompts[index] = {
            name,
            model,
            text,
            tags,
            createdAt: prompts[index].createdAt || now.toISOString(),
            updatedAt: now.toISOString()
        };
        savePrompts(prompts);
        updatePromptList();
        updatePromptSelect();
        populateTagsFilter(); //     
        document.getElementById('crocodile_prompt-name-input').value = '';
        $('#crocodile_prompt-model-select').val(null).trigger('change');
        document.getElementById('crocodile_prompt-textarea-input').value = '';
        $('#crocodile_prompt-tags-input').val(null).trigger('change');
        showAlertPrompt(' .');
    } else {
        showAlertPrompt(',   .');
    }
}

function duplicatePrompt(index) {
    const prompts = getPrompts();
    const prompt = prompts[index];
    document.getElementById('crocodile_prompt-name-input').value = prompt.name + ' ()';
    $('#crocodile_prompt-model-select').val(prompt.model).trigger('change');
    document.getElementById('crocodile_prompt-textarea-input').value = prompt.text;
    $('#crocodile_prompt-tags-input').val(prompt.tags).trigger('change');
    document.getElementById('crocodile_save-prompt-btn').textContent = ' ';
    document.getElementById('crocodile_save-prompt-btn').onclick = savePrompt;
    //    ""
    $('#crocodile_promptsSubTab a[href="#crocodile_createPromptTabContent"]').tab('show');
}

