function addSinglePromptFromPreset(index) {
    const presetName = $('#crocodile_preset-select-prompts').val();
    if (!presetName) return;

    const presets = JSON.parse(localStorage.getItem('promptPresetsAkula') || '[]');
    const preset = presets.find(p => p.name === presetName);
    if (!preset) return;

    const presetPrompt = preset.prompts[index];

    const prompts = getPrompts();
    const exists = prompts.some(p => p.name === presetPrompt.name);
    if (exists) {
        const idx = prompts.findIndex(p => p.name === presetPrompt.name);
        prompts[idx] = presetPrompt;
    } else {
        prompts.push(presetPrompt);
    }
    savePrompts(prompts);
    updatePromptList();
    updatePromptSelect();
    populateTagsFilter(); //     
    showAlertPrompt(` "${presetPrompt.name}"    .`);
}

