async function saveCurrentPromptsAsPreset() {
    const presetName = document.getElementById('crocodile_preset-name-input').value.trim();
    if (!presetName) {
        showAlertPrompt(',   .');
        return;
    }
    const prompts = getPrompts();
    if (prompts.length === 0) {
        showAlertPrompt('     .');
        return;
    }

    let presets = JSON.parse(localStorage.getItem('promptPresetsAkula') || '[]');

    // ,      
    const existingIndex = presets.findIndex(p => p.name === presetName);
    if (existingIndex !== -1) {
        const confirmOverwrite = await showConfirm(`   "${presetName}"  .  ?`);
        if (!confirmOverwrite) return;
        presets[existingIndex] = { name: presetName, prompts };
    } else {
        presets.push({ name: presetName, prompts });
    }

    localStorage.setItem('promptPresetsAkula', JSON.stringify(presets));
    populatePresetSelectPrompts();
    populatePresetSelectInManage();
    $('#crocodile_preset-select-prompts').val(presetName).trigger('change');
    $('#crocodile_preset-select-in-manage').val(presetName).trigger('change');
    showAlertPrompt('  .');
}

function PresetToJSON() {
    const presetName = $('#crocodile_preset-select-in-manage').val();
    if (!presetName) {
        showAlertPrompt('    .');
        return;
    }

    const presets = JSON.parse(localStorage.getItem('promptPresetsAkula') || '[]');
    const preset = presets.find(p => p.name === presetName);
    if (!preset) {
        showAlertPrompt('   .');
        return;
    }

    const presetJSON = JSON.stringify(preset, null, 2);
    // Copy to clipboard
    navigator.clipboard.writeText(presetJSON).then(() => {
        document.getElementById('crocodile_preset-json-input').value = presetJSON; // Optionally populate the textarea
        showAlertPrompt('JSON      .');
    }).catch(err => {
        console.error('     :', err);
        showAlertPrompt('     .');
    });
}

function clearPresetJSONField() {
    document.getElementById('crocodile_preset-json-input').value = '';
}

function importPresetFromJSONPrompts() {
    const presetJSON = document.getElementById('crocodile_preset-json-input').value;
    if (!presetJSON) {
        showAlertPrompt(' JSON  .');
        return;
    }

    try {
        const preset = JSON.parse(presetJSON);
        if (!preset.name || !preset.prompts) {
            showAlertPrompt('  .');
            return;
        }

        let presets = JSON.parse(localStorage.getItem('promptPresetsAkula') || '[]');

        // ,      
        const existingIndex = presets.findIndex(p => p.name === preset.name);
        if (existingIndex !== -1) {
            showConfirm(`   "${preset.name}"  .  ?`).then(confirmOverwrite => {
                if (!confirmOverwrite) return;
                presets[existingIndex] = preset;
                localStorage.setItem('promptPresetsAkula', JSON.stringify(presets));
                populatePresetSelectPrompts();
                populatePresetSelectInManage();
                $('#crocodile_preset-select-prompts').val(preset.name).trigger('change');
                $('#crocodile_preset-select-in-manage').val(preset.name).trigger('change');
                showAlertPrompt('  .');
            });
        } else {
            presets.push(preset);
            localStorage.setItem('promptPresetsAkula', JSON.stringify(presets));
            populatePresetSelectPrompts();
            populatePresetSelectInManage();
            $('#crocodile_preset-select-prompts').val(preset.name).trigger('change');
            $('#crocodile_preset-select-in-manage').val(preset.name).trigger('change');
            showAlertPrompt('  .');
        }
    } catch (error) {
        console.error('   :', error);
        showAlertPrompt('   .   JSON.');
    }
}

function addPresetToPrompts() {
    const presetName = $('#crocodile_preset-select-prompts').val();
    if (!presetName) {
        showAlertPrompt('    .');
        return;
    }
    const presets = JSON.parse(localStorage.getItem('promptPresetsAkula') || '[]');
    const preset = presets.find(p => p.name === presetName);
    if (!preset) return;

    const prompts = getPrompts();
    preset.prompts.forEach(presetPrompt => {
        const exists = prompts.some(p => p.name === presetPrompt.name);
        if (exists) {
            const index = prompts.findIndex(p => p.name === presetPrompt.name);
            prompts[index] = presetPrompt;
        } else {
            prompts.push(presetPrompt);
        }
    });
    savePrompts(prompts);
    updatePromptList();
    updatePromptSelect();
    populateTagsFilter(); //     
    showAlertPrompt('      .');
}


