function loadSelectedPresetPrompts() {
    const presetName = $('#crocodile_preset-select-prompts').val();
    const previewDiv = document.getElementById('crocodile_preset-preview');
    const presetCountSpan = document.getElementById('crocodile_preset-count');
    previewDiv.innerHTML = '';
    presetCountSpan.textContent = '0';

    if (!presetName) {
        return;
    }

    const presets = JSON.parse(localStorage.getItem('promptPresetsAkula') || '[]');
    const preset = presets.find(p => p.name === presetName);
    if (!preset) {
        return;
    }

    presetCountSpan.textContent = preset.prompts.length;

    if (preset.prompts.length === 0) {
        previewDiv.innerHTML = '<p class="text-muted"> .</p>';
    } else {
        previewDiv.innerHTML = ''; // Clear the previewDiv
        preset.prompts.forEach((prompt, index) => {
            const card = createPromptCard(prompt, index, {
                buttons: [
                    { text: '  ', className: 'btn-primary add-single-prompt', data: { 'prompt-index': index } },
                ],
                eventHandlers: {
                    '.add-single-prompt': (e) => addSinglePromptFromPreset(e.target.dataset.promptIndex),
                },
            });
            previewDiv.appendChild(card);
        });
    }
}



const OPENROUTER_BASE_URL = "https://openrouter.ai/api/v1";

 async function loadModels() {
    try {
        const response = await axios.get(`${OPENROUTER_BASE_URL}/models`);
        const models = response.data.data;
        localStorage.setItem('models', JSON.stringify(models));
        populateModelSelect(models);
    } catch (error) {
        console.error('Error loading models:', error);
    }
}

 async function generateTextWithAI(systemPrompt, userPrompt, model, signal) {
    const apiKey = localStorage.getItem('apiKey');

    try {
        const response = await fetch(`${OPENROUTER_BASE_URL}/chat/completions`, {
            method: 'POST',
            signal,
            headers: {
                'Authorization': `Bearer ${apiKey}`,
                'Content-Type': 'application/json',
                'HTTP-Referer': 'https://www.office.com',
                'X-Title': 'Word AI Assistant',
            },
            body: JSON.stringify({
                model: model,
                messages: [
                    { role: "system", content: systemPrompt },
                    { role: "user", content: userPrompt }
                ],
            }),
        });

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        return data.choices[0].message.content.trim();
    } catch (error) {
        console.error('Error in generateTextWithAI:', error);
        throw error;
    }
}

