 function populateModelSelect(models) {
    const promptModelSelect = $('#prompt-model');
    const formattingModelSelect = $('#formatting-model');

    [promptModelSelect, formattingModelSelect].forEach(select => {
        select.empty();
        models.forEach(model => {
            const option = new Option(model.name, model.id, false, false);
            select.append(option);
        });
        select.trigger('change');
    });

    //      formatting-model
    const defaultModel = models.find(model => model.id === 'openai/gpt-4-mini') || models[0];
    if (defaultModel) {
        formattingModelSelect.val(defaultModel.id).trigger('change');
    }

    //   ,   
    const savedModel = localStorage.getItem('formattingModel');
    if (savedModel && models.some(model => model.id === savedModel)) {
        formattingModelSelect.val(savedModel).trigger('change');
    }
}

 function initTests() {
    const testsTab = document.getElementById('tests');
    testsTab.innerHTML = `
        <h2>  </h2>
        <button id="kuga_save-selection" class="btn btn-primary mb-2"> </button>
        <button id="kuga_restore-selection" class="btn btn-success mb-2" style="display: none;"> </button>
        <button id="kuga_delete-selection" class="btn btn-danger mb-2" style="display: none;"> </button>
        <button id="kuga_highlight-selection" class="btn btn-warning mb-2" style="display: none;">   </button>
        <div id="kuga_selection-info" class="mt-2"></div>
        <div id="kuga_full-selection" class="mt-2" style="white-space: pre-wrap;"></div>
    `;

    let savedSelection = null;

    document.getElementById('kuga_save-selection').addEventListener('click', saveSelection);
    document.getElementById('kuga_restore-selection').addEventListener('click', restoreSelection);
    document.getElementById('kuga_delete-selection').addEventListener('click', deleteSelection);
    document.getElementById('kuga_highlight-selection').addEventListener('click', highlightSelection);

    async function saveSelection() {
        try {
            await Word.run(async (context) => {
                const selection = context.document.getSelection();
                selection.load('text');
                await context.sync();

                if (selection.text.trim().length > 0) {
                    const timestamp = Date.now();
                    const startMarker = `START_TIMESTAMP${timestamp}`;
                    const endMarker = `END_TIMESTAMP${timestamp}`;

                    const startRange = selection.insertText(startMarker, Word.InsertLocation.before);
                    const endRange = selection.insertText(endMarker, Word.InsertLocation.after);

                    startRange.font.size = 1;
                    endRange.font.size = 1;
                    startRange.font.color = "white";
                    endRange.font.color = "white";
                    await context.sync();

                    savedSelection = { timestamp, text: selection.text };
                    localStorage.setItem('savedSelection', JSON.stringify(savedSelection));

                    document.getElementById('kuga_selection-info').textContent = ' ';
                    document.getElementById('kuga_full-selection').textContent = selection.text;
                    document.getElementById('kuga_restore-selection').style.display = 'inline-block';
                    document.getElementById('kuga_delete-selection').style.display = 'inline-block';
                    document.getElementById('kuga_highlight-selection').style.display = 'inline-block';
                } else {
                    document.getElementById('kuga_selection-info').textContent = '  ';
                }
            });
        } catch (error) {
            console.error('   :', error);
            document.getElementById('kuga_selection-info').textContent = '   ';
        }
    }

    async function restoreSelection() {
        const saved = localStorage.getItem('savedSelection');
        if (!saved) {
            document.getElementById('kuga_selection-info').textContent = '  ';
            return;
        }

        const { timestamp } = JSON.parse(saved);

        try {
            await Word.run(async (context) => {
                const body = context.document.body;
                const startSearch = body.search(`START_TIMESTAMP${timestamp}`, { matchCase: true });
                const endSearch = body.search(`END_TIMESTAMP${timestamp}`, { matchCase: true });

                context.load(startSearch, 'items');
                context.load(endSearch, 'items');
                await context.sync();

                if (startSearch.items.length > 0 && endSearch.items.length > 0) {
                    const startRange = startSearch.items[0];
                    const endRange = endSearch.items[0];
                    const fullRange = startRange.expandTo(endRange);
                    fullRange.select();

                    document.getElementById('kuga_selection-info').textContent = ' ';
                } else {
                    document.getElementById('kuga_selection-info').textContent = '  ';
                }
            });
        } catch (error) {
            console.error('   :', error);
            document.getElementById('kuga_selection-info').textContent = `: ${error.message}`;
        }
    }

    async function highlightSelection() {
        const saved = localStorage.getItem('savedSelection');
        if (!saved) {
            document.getElementById('kuga_selection-info').textContent = '  ';
            return;
        }

        const { timestamp } = JSON.parse(saved);

        try {
            await Word.run(async (context) => {
                const body = context.document.body;
                const startSearch = body.search(`START_TIMESTAMP${timestamp}`, { matchCase: true });
                const endSearch = body.search(`END_TIMESTAMP${timestamp}`, { matchCase: true });

                context.load(startSearch, 'items');
                context.load(endSearch, 'items');
                await context.sync();

                if (startSearch.items.length > 0 && endSearch.items.length > 0) {
                    const startRange = startSearch.items[0];
                    const endRange = endSearch.items[0];
                    const fullRange = startRange.expandTo(endRange);

                    //    
                    fullRange.font.highlightColor = 'yellow';
                    await context.sync();

                    document.getElementById('kuga_selection-info').textContent = '   ';
                } else {
                    document.getElementById('kuga_selection-info').textContent = '  ';
                }
            });
        } catch (error) {
            console.error('   :', error);
            document.getElementById('kuga_selection-info').textContent = `: ${error.message}`;
        }
    }

    async function deleteSelection() {
        const saved = localStorage.getItem('savedSelection');
        if (!saved) {
            document.getElementById('kuga_selection-info').textContent = '    ';
            return;
        }

        const { timestamp } = JSON.parse(saved);

        try {
            await Word.run(async (context) => {
                const body = context.document.body;
                const startSearch = body.search(`START_TIMESTAMP${timestamp}`, { matchCase: true });
                const endSearch = body.search(`END_TIMESTAMP${timestamp}`, { matchCase: true });

                context.load(startSearch, 'items');
                context.load(endSearch, 'items');
                await context.sync();

                if (startSearch.items.length > 0 && endSearch.items.length > 0) {
                    const startRange = startSearch.items[0];
                    const endRange = endSearch.items[0];

                    startRange.delete();
                    endRange.delete();
                    await context.sync();

                    localStorage.removeItem('savedSelection');
                    savedSelection = null;

                    document.getElementById('kuga_selection-info').textContent = ' ';
                    document.getElementById('kuga_full-selection').textContent = '';
                    document.getElementById('kuga_restore-selection').style.display = 'none';
                    document.getElementById('kuga_delete-selection').style.display = 'none';
                    document.getElementById('kuga_highlight-selection').style.display = 'none';
                } else {
                    document.getElementById('kuga_selection-info').textContent = '  ';
                }
            });
        } catch (error) {
            console.error('   :', error);
            document.getElementById('kuga_selection-info').textContent = `: ${error.message}`;
        }
    }
}

