async function selectCurrentText() {
    try {
        await Word.run(async (context) => {
            const body = context.document.body;

            //  START_TIMESTAMP
            const startSearch = body.search("START_TIMESTAMP", { matchCase: true });
            startSearch.load("items");
            await context.sync();

            //  END_TIMESTAMP
            const endSearch = body.search("END_TIMESTAMP", { matchCase: true });
            endSearch.load("items");
            await context.sync();

            console.log(startSearch, endSearch)

            if (startSearch.items.length > 0 && endSearch.items.length > 0) {
                const startRange = startSearch.items[0];
                const endRange = endSearch.items[0];

                //     START_TIMESTAMP   END_TIMESTAMP
                const fullRange = startRange.expandTo(endRange);

                //  
                fullRange.select();

                console.log('  ');
                document.getElementById('kuga_selection-info').textContent = '   ';
            } else {
                console.log('  ');
                document.getElementById('kuga_selection-info').textContent = '  ';
            }
        });
    } catch (error) {
        console.error('    :', error);
        document.getElementById('kuga_selection-info').textContent = `: ${error.message}`;
    }
}



//    
async function markSelectedText(text) {
    try {
        await Word.run(async (context) => {
            const selection = context.document.getSelection();
            currentSelectionTimestamp = Date.now();
            const startMarker = `START_TIMESTAMP`;
            const endMarker = `END_TIMESTAMP`;

            selection.insertText(startMarker, Word.InsertLocation.before);
            selection.insertText(endMarker, Word.InsertLocation.after);

            const startRange = context.document.body.search(startMarker, { matchCase: true }).getFirst();
            const endRange = context.document.body.search(endMarker, { matchCase: true }).getFirst();

            startRange.font.size = 1;
            endRange.font.size = 1;
            startRange.font.color = "white";
            endRange.font.color = "white";

            await context.sync();

            localStorage.setItem('currentSelectionTimestamp', currentSelectionTimestamp);
        });
    } catch (error) {
        console.error('    :', error);
    }
}


async function getSavedSelectionRange(context) {
    const body = context.document.body;
    const startMarker = `START_TIMESTAMP`;
    const endMarker = `END_TIMESTAMP`;

    const startSearch = body.search(startMarker, { matchCase: true });
    const endSearch = body.search(endMarker, { matchCase: true });

    context.load(startSearch, 'items');
    context.load(endSearch, 'items');
    await context.sync();

    if (startSearch.items.length > 0 && endSearch.items.length > 0) {
        const startRange = startSearch.items[0];
        const endRange = endSearch.items[0];
        const fullRange = startRange.expandTo(endRange);
        return fullRange;
    } else {
        return null;
    }
}


async function finishCurrentFragment() {
    try {
        await cleanAllTimestampsGrammar();

        if(settings.removeHighlightOnFinish){
            await removeAllHighlights();
        }
        
        window.appliedCorrections = [];
        window.grammarResults = [];
        document.getElementById('grammar-results').innerHTML = '';
        document.getElementById('grammar-history').innerHTML = '';
        document.getElementById('filter-container').style.display = 'none';

        //  
        document.getElementById('select-current-text').style.display = 'none';
        document.getElementById('finish-current-fragment').style.display = 'none';

        console.log('    ');
    } catch (error) {
        console.error('     :', error);
    }
}

async function removeAllHighlights() {
    try {
        await Word.run(async (context) => {
            const fullRange = context.document.body.getRange();
            fullRange.load("text");
            await context.sync();

            const fragments = splitLongText(fullRange.text, 250);

            for (let fragment of fragments) {
                const ranges = fullRange.search(fragment, { matchCase: true, matchWholeWord: false });
                ranges.load("items");
                await context.sync();

                for (let range of ranges.items) {
                    range.font.load("highlightColor");
                    await context.sync();

                    console.log(range.font.highlightColor)

                    range.font.highlightColor = null

                    if (range.font.highlightColor === 'yellow' ||
                        range.font.highlightColor === 'green' ||
                        range.font.highlightColor === 'lightGreen') {
                        range.font.highlightColor = 'noHighlight';
                    }
                }
                await context.sync();
            }

            console.log('  ');
        });
    } catch (error) {
        console.error('   :', error);
    }
}


