    const checkLogical = getBooleanSettingGrammar('checkLogical', true);

    document.getElementById('check-grammatical').checked = checkGrammatical;
    document.getElementById('check-syntactic').checked = checkSyntactic;
    document.getElementById('check-stylistic').checked = checkStylistic;
    document.getElementById('check-logical').checked = checkLogical;

    const splitIntoChunks = getBooleanSettingGrammar('splitIntoChunks', false);
    document.getElementById('split-into-chunks-checkbox').checked = splitIntoChunks;

    const chunkSize = getNumericSettingGrammar('chunkSize', 8);
    document.getElementById('chunk-size').value = chunkSize;

    const maxChars = getNumericSettingGrammar('maxChars', 2000);
    document.getElementById('max-chars').value = maxChars;

    const temperature = getNumericSettingGrammar('temperature', 0.1);
    document.getElementById('temperature').value = temperature;

    const maxTokens = getNumericSettingGrammar('maxTokens', 4000);
    document.getElementById('max-tokens').value = maxTokens;

    const responseFormat = getBooleanSettingGrammar('responseFormat', false);
