async function cleanAllTimestampsGrammar() {
    try {
        await Word.run(async (context) => {
            const body = context.document.body;

            //     
            async function removeMarkers(prefix) {
                const searchPattern = `${prefix}*`; // , "START_TIMESTAMP*"
                const searchResults = body.search(searchPattern, { matchWildcards: true });
                searchResults.load("items");
                await context.sync();

                for (let range of searchResults.items) {
                    //      
                    range.insertText("", Word.InsertLocation.replace);
                }

                await context.sync();
            }

            //   START_TIMESTAMP{digits}
            await removeMarkers("START_TIMESTAMP");
            //   END_TIMESTAMP{digits}
            await removeMarkers("END_TIMESTAMP");

            console.log("  timestamp       .");
            document.getElementById('message').innerHTML = '<div class="alert alert-success">  </div>';
            //  3   
            setTimeout(() => {
              document.getElementById('message').innerHTML = '';
            }, 3000);
        });
    } catch (error) {
        console.error('   :', error);
        document.getElementById('message').innerHTML = `<div class="alert alert-danger">: ${error.message}</div>`;
    }
}






async function checkGrammarGrammar() {
    try {
        //     timestamp-
        await cleanAllTimestampsGrammar();
        const selectionContext = await createSelectionMarkersGrammar();

        const splitIntoChunks = document.getElementById('split-into-chunks-checkbox').checked;
        const chunkSize = parseInt(document.getElementById('chunk-size').value, 10) || 5;

        //      
        document.getElementById('grammar-results').innerHTML = '';
        resetProgressBarGrammar();

        // Clear history
        window.appliedCorrections = [];
        displayCorrectionHistory()

        showLoadingGrammar(true, splitIntoChunks);

        //   
        document.getElementById('filter-container').style.display = 'none';

        //   AbortController   
        window.abortController = new AbortController();

        const maxChars = parseInt(document.getElementById('max-chars').value, 10) || 100;

        if (selectionContext.text.length > 0) {
            if (!splitIntoChunks && maxChars > 0 && selectionContext.text.length > maxChars) {
                document.getElementById('grammar-results').innerHTML = `<p class="text-warning">    (${selectionContext.text.length} ). ,   ${maxChars}      .</p>`;
                showLoadingGrammar(false);
                //     
            document.getElementById('select-current-text').style.display = 'inline-block';
            document.getElementById('finish-current-fragment').style.display = 'inline-block'
                return;
            }

            const model = $('#grammar-model').val();
            let result = [];

            if (splitIntoChunks) {
                const chunks = splitTextIntoChunksGrammar(selectionContext.text, chunkSize);
                const totalChunks = chunks.length;
                const progressBar = document.getElementById('progress-bar');
                const progressText = document.getElementById('progress-text');

                progressText.innerText = ` 0  ${totalChunks} `;

                for (let i = 0; i < chunks.length; i++) {
                    const chunkResult = await analyzeTextWithRetryGrammar(chunks[i], model, window.abortController.signal);
                    result = result.concat(chunkResult);

                    const progressPercent = Math.round(((i + 1) / totalChunks) * 100);
                    progressBar.style.width = `${progressPercent}%`;
                    progressBar.innerText = `${progressPercent}%`;
                    progressText.innerText = ` ${i + 1}  ${totalChunks} `;
                }
            } else {
                const chunkResult = await analyzeTextWithRetryGrammar(selectionContext.text, model, window.abortController.signal);
                result = result.concat(chunkResult);
            }

            window.grammarResults = result;

            displayResultsGrammar(result);
            await highlightTextGrammar(result, selectionContext);

            if (result.length > 0) {
                document.getElementById('filter-container').style.display = 'block';
            }

            isGrammarCheckCompleted = true;
            document.getElementById('select-current-text').style.display = 'inline-block';
            document.getElementById('finish-current-fragment').style.display = 'inline-block';
        } else {
            console.log("  ");
            document.getElementById('grammar-results').innerHTML = '<p class="text-warning">,    .</p>';
            showLoadingGrammar(false);
        }
    } catch (error) {
        if (error.name === 'AbortError') {
            console.log('  .');
            document.getElementById('grammar-results').innerHTML = '<p class="text-info"> .</p>';
        } else {
            console.error('Error in checkGrammarGrammar:', error);
            document.getElementById('grammar-results').innerHTML = '<p class="text-danger">   . ,   .</p>';
        }
    } finally {
        showLoadingGrammar(false);
        window.abortController = null;
    }
}



