function displayResultsGrammar(results, scrollPosition = 0) {
    const resultsDiv = document.getElementById('grammar-results');
    resultsDiv.innerHTML = '';

    if (results.length === 0) {
        resultsDiv.innerHTML = '<p class="text-success">  .   !</p>';
        return;
    }

    //    ,    
    if (scrollPosition === 0) {
        scrollPosition = window.pageYOffset;
    }

    const filterValue = document.getElementById('filter-select').value;

    //    
    const groupedResults = {};
    results.forEach((result, index) => {
        const type = result.errorType || '';
        if (!groupedResults[type]) {
            groupedResults[type] = [];
        }
        result.index = index;

        if (filterValue === 'can_edit' && !result.can_edit) {
            return;
        }
        if (filterValue === 'cannot_edit' && result.can_edit) {
            return;
        }

        groupedResults[type].push(result);
    });

    Object.keys(groupedResults).forEach(type => {
        if (groupedResults[type].length === 0) {
            delete groupedResults[type];
        }
    });

    if (Object.keys(groupedResults).length === 0) {
        resultsDiv.innerHTML = '<p class="text-info">      .</p>';
        return;
    }

    const accordionId = 'grammarAccordion';
    let accordionHTML = `<div class="accordion" id="${accordionId}">`;

    Object.keys(groupedResults).forEach((type, index) => {
        const errors = groupedResults[type];
        const collapseId = `collapse${index}`;
        accordionHTML += `
            <div class="card">
                <div class="card-header" id="heading${index}">
                    <h2 class="mb-0">
                        <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#${collapseId}" aria-expanded="true" aria-controls="${collapseId}">
                            ${type.charAt(0).toUpperCase() + type.slice(1)}  (<span class="error-count">${errors.length}</span>)
                        </button>
                    </h2>
                </div>
                <div id="${collapseId}" class="collapse show" aria-labelledby="heading${index}" data-parent="#${accordionId}">
                    <div class="card-body">
                        ${errors.map((error, errorIndex) => `
                            <div class="mb-3 p-3 border error-card ${errorIndex < errors.length - 1 ? 'border-bottom' : ''}" data-error-index="${error.index}" data-error-type="${type}">
                                <p><strong> :</strong> <span class="text-danger">${error.text}</span></p>
                                <p><strong>:</strong> ${error.comment}</p>
                                ${error.can_edit ?
                                `<p><strong>:</strong> <span class="text-success">${error.text_to_change}</span></p>
                                <div class="btn-group" role="group">
                                    <button class="btn btn-sm btn-primary apply-suggestion" data-error-index="${error.index}"> </button>
                                    <button class="btn btn-sm btn-secondary mark-as-resolved" data-error-index="${error.index}">  </button>
                                </div>`
                                 :
                                `<p><strong>:</strong> ${error.originalComment || error.suggestion}</p>
                                <p><strong>  :</strong> <span class="text-info">${error.example || ''}</span></p>
                                ${error.originalComment ?
                                    `<p class="text-warning"><em> , ..      .</em></p>`
                                    :
                                    `<p><em>   .</em></p>`
                                }
                                <button class="btn btn-sm btn-secondary mark-as-resolved" data-error-index="${error.index}">  </button>`
                                }
                            </div>
                        `).join('')}
                    </div>
                </div>
            </div>
        `;
    });

    accordionHTML += '</div>';
    resultsDiv.innerHTML = accordionHTML;

    //     " "
    const applyButtons = document.getElementsByClassName('apply-suggestion');
    for (let button of applyButtons) {
        button.addEventListener('click', function () {
            const errorIndex = parseInt(this.dataset.errorIndex, 10);
            applySuggestion(errorIndex, results, this);
        });
    }

    //     "  "
    const resolveButtons = document.getElementsByClassName('mark-as-resolved');
    for (let button of resolveButtons) {
        button.addEventListener('click', function () {
            const errorIndex = parseInt(this.dataset.errorIndex, 10);
            markAsResolved(errorIndex, results, this);
        });
    }

    //    
    const lastErrorCard = document.querySelector('.error-card:last-child');
    if (lastErrorCard) {
        lastErrorCard.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }
}



async function highlightTextGrammar(results, selectionContext) {
    const highlight = document.getElementById('highlight-checkbox').checked;
    const addComments = document.getElementById('comment-checkbox').checked;
    if (!highlight && !addComments) return;

    await Word.run(async (context) => {
        const fullRange = await getSavedSelectionRange(context, selectionContext.timestamp);
        if (!fullRange) {
            console.error('    ');
            return;
        }

        for (let result of results) {
            try {
                const fragments = splitLongText(result.text, 250);

                for (let fragment of fragments) {
                    const ranges = fullRange.search(fragment, { matchCase: true, matchWholeWord: false });
                    ranges.load("items");
                    await context.sync();

                    if (ranges.items.length > 0) {
                        for (let range of ranges.items) {
                            if (highlight) {
                                range.font.highlightColor = "yellow";
                            }
                            if (addComments && fragment === fragments[0]) {
                                let commentText = ` : ${result.errorType}\n: ${result.comment}`;
                                if (result.can_edit) {
                                    commentText += `\n: ${result.text_to_change}`;
                                } else {
                                    commentText += `\n: ${result.suggestion}\n  : ${result.example}`;
                                }
                                range.insertComment(commentText);
                            }
                        }
                    }
                }
                await context.sync();
            } catch (error) {
                console.log(`Error processing result: ${result.text}`, error);
            }

            await new Promise(resolve => setTimeout(resolve, 100));
        }
    }).catch(function (error) {
        console.log('Error in highlightTextGrammar:', error);
    });
}

async function createSelectionMarkersGrammar() {
    try {
        return await Word.run(async (context) => {
            const selection = context.document.getSelection();
            selection.load("text");
            await context.sync();

            const selectionText = selection.text.trim();
            if (selectionText.length === 0) {
                throw new Error("  ");
            }

            const startMarker = "START_TIMESTAMP";
            const endMarker = "END_TIMESTAMP";

            selection.insertText(startMarker, Word.InsertLocation.before);
            selection.insertText(endMarker, Word.InsertLocation.after);

            const startRange = context.document.body.search(startMarker, { matchCase: true }).getFirst();
            const endRange = context.document.body.search(endMarker, { matchCase: true }).getFirst();

            startRange.font.size = 1;
            endRange.font.size = 1;
            startRange.font.color = "white";
            endRange.font.color = "white";

            await context.sync();

            return {
                text: selectionText
            };
        });
    } catch (error) {
        console.error('    :', error);
        throw error;
    }
}




function splitLongText(text, maxLength) {
    const words = text.split(' ');
    const fragments = [];
    let currentFragment = '';

    for (let word of words) {
        if ((currentFragment + ' ' + word).length > maxLength && currentFragment.length > 0) {
            fragments.push(currentFragment.trim());
            currentFragment = '';
        }
        currentFragment += (currentFragment ? ' ' : '') + word;
    }

    if (currentFragment.length > 0) {
        fragments.push(currentFragment.trim());
    }

    return fragments;
}


