  async function applySuggestion(errorIndex, results, buttonElement) {
    const error = results[errorIndex];
    if (!error) return;

    if (!error.can_edit) {
        displayErrorMessage(buttonElement, '      .');
        return;
    }

    // if (!isSimpleReplacement(error.text, error.text_to_change)) {
    //     displayErrorMessage(buttonElement, '         .');
    //     return;
    // }

    await Word.run(async (context) => {
        const body = context.document.body;
        const searchResults = body.search(error.text, { matchCase: true, matchWholeWord: false });
        searchResults.load("items");
        await context.sync();

        if (searchResults.items.length > 0) {
            for (let range of searchResults.items) {
                range.insertText(error.text_to_change, Word.InsertLocation.replace);

                const highlightCorrected = document.getElementById('highlight-corrected-checkbox').checked;
                if (highlightCorrected) {
                    range.font.highlightColor = 'lightgreen';
                }
            }
            await context.sync();

            //    
            addToHistory(error);

            //     
            results.splice(errorIndex, 1);

            //   
            displayResultsGrammar(results);

            //   
            displayCorrectionHistory();

            //     
            await checkRemainingCorrections(results);

            // ,   
            checkIfNoErrorsLeft();
        } else {
            displayErrorMessage(buttonElement, '     . ,     .');
        }
    }).catch(function (error) {
        console.log('Error:', error);
        displayErrorMessage(buttonElement, '    . ,   .');
    });
}

async function checkRemainingCorrections(results) {
    await Word.run(async (context) => {
        const body = context.document.body;
        body.load("text");
        await context.sync();

        const originalContent = body.text;

        for (let result of results) {
            if (result.can_edit) {
                const ranges = body.search(result.text, { matchCase: true, matchWholeWord: false });
                ranges.load("items");
                await context.sync();

                if (ranges.items.length === 0) {
                    result.can_edit = false;
                    if (!result.originalComment) {
                        result.originalComment = result.comment;
                        result.originalSuggestion = result.suggestion;
                        result.originalExample = result.example;
                    }
                    result.comment = " ,        .";
                }
            }
        }

        //      
        displayResultsGrammar(results);
    });
}



function displayCorrectionHistory() {
    const historyDiv = document.getElementById('grammar-history');
    historyDiv.innerHTML = '';

    if (appliedCorrections.length === 0) {
        historyDiv.innerHTML = '<p class="text-info">  .</p>';
        return;
    }

    //     
    const groupedCorrections = appliedCorrections.reduce((acc, correction) => {
        const type = correction.errorType || '';
        if (!acc[type]) {
            acc[type] = [];
        }
        acc[type].push(correction);
        return acc;
    }, {});

    let accordionHTML = `<div class="accordion" id="historyAccordion">`;

    Object.keys(groupedCorrections).forEach((type, index) => {
        const corrections = groupedCorrections[type];
        const collapseId = `historyCollapse${index}`;

        accordionHTML += `
            <div class="card">
                <div class="card-header" id="historyHeading${index}">
                    <h2 class="mb-0">
                        <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#${collapseId}" aria-expanded="true" aria-controls="${collapseId}">
                            ${type.charAt(0).toUpperCase() + type.slice(1)}  (<span class="correction-count">${corrections.length}</span>)
                        </button>
                    </h2>
                </div>
                <div id="${collapseId}" class="collapse show" aria-labelledby="historyHeading${index}" data-parent="#historyAccordion">
                    <div class="card-body">
                        ${corrections.map((correction, correctionIndex) => `
                            <div class="mb-3 p-3 border correction-card ${correctionIndex < corrections.length - 1 ? 'border-bottom' : ''}">
                                <p><strong> :</strong> <span class="text-danger">${correction.text}</span></p>
                                <p><strong>:</strong> ${correction.comment}</p>
                                ${correction.can_edit ?
                                    `<p><strong>:</strong> <span class="text-success">${correction.text_to_change}</span></p>` :
                                    `<p><strong>:</strong> ${correction.suggestion}</p>
                                     <p><strong>  :</strong> <span class="text-info">${correction.example}</span></p>`
                                }
                                ${correction.resolvedManually ?
                                    `<p class="text-info"><em>   </em></p>` :
                                    `<p class="text-success"><em> </em></p>`
                                }
                            </div>
                        `).join('')}
                    </div>
                </div>
            </div>
        `;
    });

    accordionHTML += '</div>';
    historyDiv.innerHTML = accordionHTML;

    //    
    const lastCorrectionCard = document.querySelector('.correction-card:last-child');
    if (lastCorrectionCard) {
        lastCorrectionCard.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }
}





function checkIfNoErrorsLeft() {
    const accordion = document.getElementById('grammarAccordion');
    if (!accordion || !accordion.children.length) {
        //   ,  
        const resultsDiv = document.getElementById('grammar-results');
        resultsDiv.innerHTML = '<p class="text-success">  !</p>';
        //   
        document.getElementById('filter-container').style.display = 'none';
    }
}

function isSimpleReplacement(originalText, replacementText) {
    // ,      
    // ,      
    const originalSentences = (originalText.match(/[.!?]/g) || []).length;
    const replacementSentences = (replacementText.match(/[.!?]/g) || []).length;

    if (replacementSentences > originalSentences + 1) {
        return false;
    }

    if (replacementText.length > originalText.length * 2) {
        return false;
    }

    return true;
}

function displayErrorMessage(buttonElement, message) {
    // ,  buttonElement 
    if (!buttonElement) return;

    //     
    const existingErrorMessage = buttonElement.parentElement.querySelector('.error-message');
    if (existingErrorMessage) {
        existingErrorMessage.remove();
    }

    //       
    const errorMessageElement = document.createElement('div');
    errorMessageElement.className = 'error-message text-danger mt-2';
    errorMessageElement.textContent = message;

    //      
    buttonElement.parentElement.insertBefore(errorMessageElement, buttonElement.nextSibling);
}



